.topheader {
  background-color: #174c80;
}
.navbar {
  background-color: #174c80;
}
.navbar-item {
  color: #fff;
}
.results--section {
  padding: 20px 0px;
  margin-top: 40px;
}
h1 {
  text-align: center;
  font-size: 30px;
}

.input-label:focus ~ label,
.input-label:not(:placeholder-shown) ~ label {
  left: 7px !important;
  top: -7px !important;
  width: max-content !important;
  padding: 0px 5px !important;
  color: #757488 !important;
  position: absolute !important;
  font-size: 0.7rem !important;
  height: 8px !important;
}

.dark-label {
  background-color: #21212a;
}

.light-label {
  background-color: white;
}

.button-label {
  font-weight: bold;
  letter-spacing: 1px;
}

.card_body {
  max-height: 290px;
  padding: 0px 2rem 0px 1.5rem;
  overflow-y: overlay;
}

.card_body::-webkit-scrollbar {
  width: 10px;
}

.card_body::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #9898a6;
  border-radius: 30px;
}
